body {
  background: url("cat.7a75c089.jpg");
}

.container {
  flex-direction: column;
  align-items: center;
  max-width: 1192px;
  margin: 50px auto 0;
  display: flex;
}

.hide {
  display: none;
}

.breed-select {
  width: 500px;
}

.cat-info {
  background-color: #f0f8ff;
  width: 500px;
  padding: 15px;
}

.loader {
  background-color: #00000080;
  width: 64px;
  height: 64px;
  position: relative;
  overflow: hidden;
  transform: rotate(45deg);
}

.loader:after {
  content: "";
  background: #222b32;
  margin: auto;
  position: absolute;
  inset: 8px;
}

.loader:before {
  content: "";
  background: #de3500;
  margin: auto;
  animation: 2s linear infinite diamondLoader;
  position: absolute;
  inset: -15px;
}

@keyframes diamondLoader {
  0%, 10% {
    transform: translate(-64px, -64px)rotate(-45deg);
  }

  90%, 100% {
    transform: translate(0)rotate(-45deg);
  }
}

